import logo from '/newreactsite/src/logo.svg';
import { useSpring, animated } from '@react-spring/web';

function Footer() {
    const rotateInf = useSpring({
        from: {rotate: 0},
        to: {rotate: 360},
        loop: true,
        config: {duration: 20000},
    });
    return (
        <div className="container">
            <div id='logo' className='w-16 m-auto'>
                <animated.img style={{...rotateInf}} src={logo} className="App-logo" alt="logo"></animated.img>
            </div>
            <p className='text-white text-center'>Edit <code>src/App.js</code> and save to reload.</p>
        </div>
    );
}

export default Footer;