import logo from '../logo.svg';
import SimpleParallax from "simple-parallax-js";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {fa1, fa2, fa3, fa4} from '@fortawesome/free-solid-svg-icons';
import {animated, useInView} from '@react-spring/web';
import React, {useState, useEffect} from 'react';
import {Modal, ModalCta} from '../components/popups/Ctapop'
import { Link } from 'react-router-dom';

// Elastic animation
export const elastic = 'linear(0 0%, 0.22 2.1%, 0.86 6.5%, 1.11 8.6%, 1.3 10.7%, 1.35 11.8%, 1.37 12.9%, 1.37 13.7%, 1.36 14.5%, 1.32 16.2%, 1.03 21.8%, 0.94 24%, 0.89 25.9%, 0.88 26.85%, 0.87 27.8%, 0.87 29.25%, 0.88 30.7%, 0.91 32.4%, 0.98 36.4%, 1.01 38.3%, 1.04 40.5%, 1.05 42.7%, 1.05 44.1%, 1.04 45.7%, 1 53.3%, 0.99 55.4%, 0.98 57.5%, 0.99 60.7%, 1 68.1%, 1.01 72.2%, 1 86.7%, 1 100%';

// About content
export const AboutContent = () => {

    const [ref, inView] = useInView(
        () => ({
            from: {
                opacity: 0,
                x: 100,
            },
            to: {
                opacity: 1,
                x: 0,
            },
        }),
        {rootMargin: '-25% 0%',
        }
    )
    
    const [ref2, inView2] = useInView(
        () => ({
            from: {
                opacity: 0,
                x: -100,
            },
            to: {
                opacity: 1,
                x: 0,
            },
        }),
        {rootMargin: '-20% 0%',
        }
    )

    const [ref3, inView3] = useInView(
        () => ({
            from: {
                opacity: 0,
                transform: 'scale(0)',
            },
            to: {
                opacity: 1,
                transform: 'scale(1)',
            },
        }),
        {
        rootMargin: '-10% 0%',
        }
    )

    const [ref4, inView4] = useInView(
        () => ({
            from: {
                opacity: 0,
                transform: 'scale(0)',
            },
            to: {
                opacity: 1,
                transform: 'scale(1)',
            },
        }),
        {
        rootMargin: '-10% 0%',
        }
    )

    const [ref5, inView5] = useInView(
        () => ({
            from: {
                opacity: 0,
                transform: 'scale(0)',
            },
            to: {
                opacity: 1,
                transform: 'scale(1)',
            },
        }),
        {
        rootMargin: '-10% 0%',
        }
    )

    const [ref6, inView6] = useInView(
        () => ({
            from: {
                opacity: 0,
                transform: 'scale(0)',
            },
            to: {
                opacity: 1,
                transform: 'scale(1)',
            },
        }),
        {
        rootMargin: '-10% 0%',
        }
    )
    //Modal
    const [isModalOpen, setModalOpen] = useState(false);

    const openModal = () => setModalOpen(true);
    const closeModal = () => setModalOpen(false);

    useEffect(() => {
        if (isModalOpen) {
          document.documentElement.classList.add('modal-open');
        } else {
          document.documentElement.classList.remove('modal-open');
        }
    
        return () => document.documentElement.classList.remove('modal-open');
      }, [isModalOpen]);

    //Modal CTA
    const [isModalCtaOpen, setModalCtaOpen] = useState(false);

    const openModalCta = () => setModalCtaOpen(true);
    const closeModalCta = () => setModalCtaOpen(false);

    useEffect(() => {
        if (isModalCtaOpen) {
          document.documentElement.classList.add('modal-open');
        } else {
          document.documentElement.classList.remove('modal-open');
        }
    
        // Clean up by removing the class when the component unmounts or `isModalOpen` changes
        return () => document.documentElement.classList.remove('modal-open');
      }, [isModalCtaOpen]);

    const ViewMore = () => {
        const [isVisible, setIsVisible] = useState(false);

        useEffect(() => {
            const checkClass = () => {
                setIsVisible(!document.body.classList.contains('page-about'));
            };
            checkClass();
        });

        return (
            <>
            {isVisible && (
                <button><Link className="block px-4 py-2 mb-1 bg-[#008b8b] text-[#f0f3f5] uppercase shadow-[0_4px_6px_0_rgba(0,0,0,0.5)] pointer rounded-md text-sm" to="/about">View More</Link></button>
            )}
            </>
        )
    }
    return (
        <div className="container">
                <div className='m-4'>
                    <h1 className="text-center text-3xl font-bold"><span>What is</span> Lorem Ipsum?</h1>
                        <animated.div className='aboutimg block max-w-96 w-full m-auto bg-[radial-gradient(circle,_rgba(2,0,36,1)_0%,_rgba(39,43,54,1)_35%,_rgba(0,212,255,1)_100%)] rounded-full float-none sm:w-1/2 sm:float-left sm:mr-4 sm:mb-4'
                         ref = {ref}
                         style={{
                            transition: elastic,
                            ...inView}}
                        >
                            <SimpleParallax scale={1.4} overflow={1} transition={elastic}>
                                <img className='object-cover aspect-square' loading="lazy" src={logo} alt='logo' />
                            </SimpleParallax>
                        </animated.div>
                        <div className='about-content'>
                            <animated.p
                                ref={ref2}
                                style={{
                                    transition: elastic,
                                    ...inView2}}
                             ><b>Lorem Ipsum</b> is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</animated.p>
                            <div className='about-icons mb-4 relative float-left'>
                                <div className='icon-stage flex flex-wrap'>
                                    <animated.div className='icon-content w-1/2 text-center'
                                        ref={ref3}
                                        style={{
                                            transition: elastic,
                                            ...inView3}}
                                    >
                                        <h2 className='text-2xl uppercase m-0'>One</h2>
                                        <div className='icon-img bg-[#008b8b] text-[#f7f7f7] rounded-full flex justify-center items-center w-24 mx-auto aspect-square shadow-[0_0_5px_2px_#61dafb]'><span><FontAwesomeIcon icon={fa1} size='2xl'></FontAwesomeIcon></span></div>
                                        <p className='text-[66.66%] font-medium p-3'><b>Lorem Ipsum</b> is simply dummy text of the printing and typesetting industry.</p>
                                    </animated.div>
                                    <animated.div className='icon-content w-1/2 text-center'
                                        ref={ref4}
                                        style={{
                                            transition: elastic,
                                            ...inView4}}
                                    >
                                        <h2 className='text-2xl uppercase m-0'>Two</h2>
                                        <div className='icon-img bg-[#008b8b] text-[#f7f7f7] rounded-full flex justify-center items-center w-24 mx-auto aspect-square shadow-[0_0_5px_2px_#61dafb]'><span><FontAwesomeIcon icon={fa2} size='2xl'></FontAwesomeIcon></span></div>
                                        <p className='text-[66.66%] font-medium p-3'><b>Lorem Ipsum</b> is simply dummy text of the printing and typesetting industry.</p>
                                    </animated.div>
                                    <animated.div className='icon-content w-1/2 text-center'
                                        ref={ref5}
                                        style={{
                                            transition: elastic,
                                            ...inView5}}
                                    >
                                        <h2 className='text-2xl uppercase m-0'>Three</h2>
                                        <div className='icon-img bg-[#008b8b] text-[#f7f7f7] rounded-full flex justify-center items-center w-24 mx-auto aspect-square shadow-[0_0_5px_2px_#61dafb]'><span><FontAwesomeIcon icon={fa3} size='2xl'></FontAwesomeIcon></span></div>
                                        <p className='text-[66.66%] font-medium p-3'><b>Lorem Ipsum</b> is simply dummy text of the printing and typesetting industry.</p>
                                    </animated.div>
                                    <animated.div className='icon-content w-1/2 text-center'
                                        ref={ref6}
                                        style={{
                                            transition: elastic,
                                            ...inView6}}
                                    >
                                        <h2 className='text-2xl uppercase m-0'>Four</h2>
                                        <div className='icon-img bg-[#008b8b] text-[#f7f7f7] rounded-full flex justify-center items-center w-24 mx-auto aspect-square shadow-[0_0_5px_2px_#61dafb]'><span><FontAwesomeIcon icon={fa4} size='2xl'></FontAwesomeIcon></span></div>
                                        <p className='text-[66.66%] font-medium p-3'><b>Lorem Ipsum</b> is simply dummy text of the printing and typesetting industry.</p>
                                    </animated.div>
                                </div>
                            </div>
                            <h2 className='text-xl font-semibold'><span>Where does it</span> come from?</h2>
                            <p>Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32.</p>
                            <p>The standard chunk of Lorem Ipsum used since the 1500s is reproduced below for those interested. Sections 1.10.32 and 1.10.33 from "de Finibus Bonorum et Malorum" by Cicero are also reproduced in their exact original form, accompanied by English versions from the 1914 translation by H. Rackham.</p>
                        </div>
                        <div className='button'>
                            <button className="block px-4 py-2 mb-2 bg-[#008b8b] text-[#f0f3f5] uppercase shadow-[0_4px_6px_0_rgba(0,0,0,0.5)] pointer rounded-md text-sm" onClick={openModal}>Open Modal</button>
                            <Modal show={isModalOpen} onClose={closeModal} />
                        </div>
                        <div className='button'>
                            <button className="block px-4 py-2 mb-2 bg-[#008b8b] text-[#f0f3f5] uppercase shadow-[0_4px_6px_0_rgba(0,0,0,0.5)] pointer rounded-md text-sm" onClick={openModalCta}>Open CTA</button>
                            <ModalCta show={isModalCtaOpen} onClose={closeModalCta} />
                        </div>
                        <div className='button'>
                            <ViewMore/>
                        </div>
                </div>
            </div>
    )
}

const About = () => {
    return (
        <>
        <div id="main">
            <div className="about-page-section">
                <AboutContent/>
            </div>
        </div>
        </>
    );
}

export default About;