import banner1 from '../components/images/banner1.webp';
import banner2 from '../components/images/banner2.webp';
import banner3 from '../components/images/banner3.webp';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import '../Carousel.css';
import {AboutContent} from './About';
import { GalleryContent } from './Gallery';
/* import { ProductCategoriesList } from './ProductCategories'; */
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faCircleDot} from '@fortawesome/free-solid-svg-icons';

export const Carousel = () => {
    const settings = {
        fade: true,
        cssEase: 'ease-in-out',
        dots: true,
        customPaging: i => (<FontAwesomeIcon icon={faCircleDot} color='lightgrey' size='xs'/>),
        infinite: true,
        speed: 300,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        autoplay: true,
        pauseOnFocus: true,
        lazyLoad: 'ondemand',
        autoplaySpeed: 6000,
        responsive: [
            {
            breakpoint: 768,
            settings: {
                arrows: false
                }
            }
        ]
    };

    return (
        <section className="carousel-wrapper w-full bg-gray-900 relative overflow-hidden">
            {/* <SimpleParallax scale={2} transition={'ease'}>
                <img src={bannerpattern} alt='banner-pattern' />
            </SimpleParallax> */}
            <Slider className='homeBanner' {...settings}>
                <div>
                    <img loading="lazy" src={banner1} alt="Banner 1" />
                    <div className='caption'><h2 className='text-center text-2xl'><span className='text-[#f0f3f5] invert-[100]'>Caption</span> for banner 1</h2></div>
                </div>
                <div>
                    <img loading="lazy" src={banner2} alt="Banner 2" />
                    <div className='caption'><h2 className='text-center text-2xl'><span className='text-[#f0f3f5] invert-[100]'>Caption</span> for banner 2</h2></div>
                </div>
                <div>
                    <img loading="lazy" src={banner3} alt="Banner 3" />
                    <div className='caption'><h2 className='text-center text-2xl'><span className='text-[#f0f3f5] invert-[100]'>Caption</span> for banner 3</h2></div>
                </div>
            </Slider>
        </section>
    );
};

const Home = () => {
    return (
        <div id='main'>
            <Carousel/>
            {/* <section className='product-categories-section'>
                <h2 className='text-center text-2xl font-semibold'>Product Categories</h2>
                <ProductCategoriesList/>
            </section> */}
            <section className='about-section mb-10'>
                <AboutContent/>
            </section>
            <section className='gallery-section mb-10'>
            <h2 className="text-center text-xl font-bold"><span>Check Our </span>ins and outs</h2>
                <GalleryContent/>
            </section>
        </div>
    );
}

export default Home;