// Add page class to Body
const usePageClass = () => {
  const location = useLocation();

  useEffect(() => {
    // Extract page name from the current path
    const pageName = location.pathname.replace("/", "") || "home";
    const parser = new UAParser();
    const browser = parser.getBrowser();
    const device = parser.getDevice();
    const os = parser.getOS();

    const classes = [];
    
    // Set body class
    document.body.className = `page-${pageName}`;
    if (browser.name) classes.push(browser.name.toLowerCase().replace(/\s+/g, "-").replace(/[^a-z0-9\-]/g, ""));
    if (browser.version) classes.push(browser.version.split(".")[0]);
    if (os.name) classes.push(os.name.toLowerCase().replace(/\s/g, "-"));
    if (os.version) classes.push(os.version.replace(/\s/g, "-"));
    if (device.model) classes.push(device.model.toLowerCase().replace(/\s/g, "-"));
    if (device.vendor) classes.push(device.vendor.toLowerCase().replace(/\s/g, "-"));
    if (device.type) classes.push(device.type.toLowerCase());
    else classes.push("desktop");
    document.documentElement.classList.add(...classes);
    
    // Cleanup on unmount
    return () => {
      document.body.className = "";
      document.documentElement.classList.remove(...classes);
    };
  }, [location]); // Run when location changes
};

// Scroll to top on page change
const ScrollToTop = (props) => {
    const location = useLocation();
    useEffect(() => {
      if (!location.hash) {
        window.scrollTo(0, 0);
      }
    }, [location]);
  
    return <>{props.children}</>
  };

