import {useLocation, Link} from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDoubleRight } from '@fortawesome/free-solid-svg-icons';

const breadcrumbNameMap = {'/about': 'About','/gallery': 'Gallery',};

const Breadcrumbs = () => {
    const location = useLocation();
    const pathnames = location.pathname.split('/').filter(x => x);

    if (pathnames.length === 0) return null;

    return (
        <div className='container'>
            <nav className="mx-4 py-1 text-sm text-left text-[#ffffff] font-bold">
            <Link to="/">Home</Link>
            {pathnames.map((value, index) => {
                const to = '/' + pathnames.slice(0, index + 1).join('/');
                const isLast = index === pathnames.length - 1;
                const name = breadcrumbNameMap[`/${value}`] || decodeURIComponent(value);

                return (
                <span key={to}>
                {<FontAwesomeIcon className='mx-2' icon={faAngleDoubleRight}/>}
                {isLast ? (
                    <span className='opacity-50'>{name}</span>
                ) : (
                    <Link to={to}>{name}</Link>
                )}
                </span>
                );
            })}
            </nav>
        </div>
    );
};

export default Breadcrumbs;