import React from 'react';
import { useSpring, animated } from '@react-spring/web';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faTimes} from '@fortawesome/free-solid-svg-icons';

export const Modal = ({ show, onClose }) => {
  const springProps = useSpring({
    opacity: show ? 1 : 0,
    transform: show ? 'translateY(0)' : 'translateY(-50px)',
    config: { tension: 250, friction: 20 },
  });

  if (!show) return null;

  return (
    <div className="modal-overlay fixed bg-[#00000080] flex justify-center items-center z-50 top-0 left-0 w-full h-full">
      <animated.div className="modal-content relative bg-white p-4 rounded-md max-w-lg w-full" style={springProps}>
        <button className="close-button px-4 py-2 absolute top-2 right-2 text-sm text-sm bg-[#008b8b] text-[#f0f3f5] uppercase rounded-md sm:text-lg" onClick={onClose}><FontAwesomeIcon icon={faTimes}/></button>
        <h2>Modal Title</h2>
        <p>This is a modal popup content.</p>
      </animated.div>
    </div>
  );
};

