import logo from '../logo.webp';
import Menu from './Menu';
import Breadcrumbs from './Breadcrumbs';
import { useSpring, animated } from '@react-spring/web';

const Header = () => {
  const rotateInf = useSpring({
    from: {rotate: 0},
    to: {rotate: 360},
    loop: true,
    config: {duration: 20000},
  });
  return (
    <div className="App z-50 relative">
        <header className="App-header flex justify-between items-center px-2 bg-[#272b36] fixed w-full left-0 top-0 z-40 transition-[all_0.3s_ease-in-out]">
          <div id='logo' className='w-24'>
            <a href='/newreactsite'>
              <animated.img style={{...rotateInf}} loading="lazy" height="96" width="96" src={logo} className="App-logo p-2" alt="logo"></animated.img>
            </a>
          </div>
          <div className='menu'>
            <Menu />
          </div>
        </header>
        <div className='breadcrumbs bg-[#272b36] fixed w-full top-24'>
          <Breadcrumbs/>
        </div>
    </div>
  );
}

export default Header;