import React, {useState, useEffect} from 'react';
import Slider from 'react-slick';
import banner1 from '../components/images/banner1.webp';
import banner2 from '../components/images/banner2.webp';
import banner3 from '../components/images/banner3.webp';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faCircleDot, faTimes} from '@fortawesome/free-solid-svg-icons';
import { useTransition, animated } from '@react-spring/web';
import { Link } from 'react-router-dom';

const galleryImages = [banner1,banner2,banner3];

export const GalleryContent = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [initialSlide, setInitialSlide] = useState(0);

    useEffect(() => {
        if (isOpen) {
            document.documentElement.classList.add('modal-open');
        } else {
            document.documentElement.classList.remove('modal-open');
        }
        return () => document.documentElement.classList.remove('modal-open');
        }, [isOpen]);

    const settings = {
        initialSlide,
        cssEase: 'ease-in-out',
        dots: true,
        customPaging: i => (<FontAwesomeIcon icon={faCircleDot} color='lightgrey' size='xs'/>),
        infinite: true,
        speed: 300,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        lazyLoad: 'ondemand'
    };

    const transitionProps = useTransition(isOpen, {
        from: { opacity: 0, transform: 'scale(0.9)' },
        enter: { opacity: 1, transform: 'scale(1)' },
        leave: { opacity: 0, transform: 'scale(0.9)' },
        config: { tension: 250, friction: 20 },
    });

    const ViewMore = () => {
        const [isVisible, setIsVisible] = useState(false);

        useEffect(() => {
            const checkClass = () => {
                setIsVisible(!document.body.classList.contains('page-gallery'));
            };
            checkClass();
        });

        return (
            <>
            {isVisible && (
                <button className='block mx-auto'><Link className='block px-4 py-2 bg-[#008b8b] text-[#f0f3f5] uppercase shadow-[0_4px_6px_0_rgba(0,0,0,0.5)] pointer rounded-md text-sm' to="/gallery">View More</Link></button>
            )}
            </>
        )
    }
    return (
        <>
        <div className="container">
            <div className="m-4">
                <div className="grid sm:grid-cols-3 gap-4 grid-cols-2">
                    {galleryImages.map((img, index) => (
                    <img
                        key={index}
                        loading="lazy"
                        src={img}
                        alt={`Gallery image-${index}`}
                        onClick={() => {
                        setInitialSlide(index);
                        setIsOpen(true);
                        }}
                        className="cursor-pointer pointer-events-auto aspect-square object-cover bg-[#272b36] bg-opacity-25 rounded-full"
                    />
                    ))}
                </div>
                {transitionProps((style, item) =>
                item ? (
                    <animated.div className="fixed inset-0 z-50 bg-black bg-opacity-90 flex justify-center items-center" style={style}>
                    <button className="close-button px-4 py-2 absolute top-2 right-2 text-sm bg-[#008b8b] text-[#f0f3f5] uppercase rounded-md sm:text-lg" onClick={() => setIsOpen(false)}><FontAwesomeIcon icon={faTimes}/></button>
                    <div className="w-full">
                        <Slider {...settings}>
                        {galleryImages.map((img, i) => (
                            <div key={i}>
                            <img loading="lazy" src={img} className="w-full max-h-[80vh] object-contain" />
                            </div>
                        ))}
                        </Slider>
                    </div>
                    </animated.div>
                ) : null
                )}
            </div>
            <ViewMore/>
        </div>
        </>
    )
}

const Gallery = () => {
    return (
        <div id="main">
            <div className="gallery-page-section">
            <h1 className="text-center text-3xl font-bold"><span>Check Our </span>ins and outs</h1>
                <GalleryContent/>
            </div>
        </div>
    );
}

export default Gallery;